.modalCover{position:absolute;width:100%;height:100vh;overflow:hidden;top:-120vh;left:0;background:rgba(0,0,0,.1);backdrop-filter:blur(1px)}.modalCover__active{top:-105px;transition:top 0s 2s}.modal{position:absolute;width:300px;top:60px;left:calc(50% - 150px);padding:10px 5px;text-align:center;font-size:20px;background-color:#ffecad;border:2px solid #616161;border-radius:8px;z-index:5}.modal .modalTextWin{font-size:1em;letter-spacing:3px;padding-bottom:15px;border-bottom:2px dotted #000}.modal .modalTextWin::before{content:"😎"}.modal .modalTextLose{margin-bottom:10px;border-bottom:2px dotted #000}.modal .modalTextLose::before{content:"🤨"}.modal .dailyWord{font-size:40px;letter-spacing:5px;text-shadow:1px 1px 2px #25db1f}.modal .newGamebtn{border:2px dotted #616161;border-radius:5px;padding:5px 10px}.modal .closeBtn{position:absolute;top:10px;right:10px;user-select:none}@media(min-width: 500px){.modal{padding:20px 5px;width:450px;left:calc(50% - 225px);font-size:24px}.modal .modalTextWin{border-bottom:4px dotted #000}.modal .modalTextLose{border-bottom:4px dotted #000}.modal .dailyWord{font-size:50px;letter-spacing:5px}.modal .statsContainer{padding:15px}}